/* BOOSTRAP CUSTOMIZATION
================================================== */

.background-primary {
  background-color: #212121 !important;
}

.color-primary {
  color: var(--primarycolor) !important;
}

.pay-disabled {
  pointer-events: none;
  opacity: 0.5;
}

.fixed-top {
  z-index: 100;
}

.navbar {
  margin-bottom: 0;
}

.navbar-default {
  background: none;
  border: none;
}

.page-content p {
  word-break: break-word;
}

.img-responsive {
  max-width: 100%;
  height: auto;
  vertical-align: top;
}

.w-20 {
  width: 20% !important;
}

.w-33 {
  width: 33% !important;
}

.w-40 {
  width: 40% !important;
}

.w-60 {
  width: 60% !important;
}

.bounce {
  animation: bounce 2s ease;

  @keyframes bounce {
    0%,
    20%,
    50%,
    80%,
    100% {
      transform: translateY(0);
    }
    40% {
      transform: translateY(-15px);
    }
    60% {
      transform: translateY(-7px);
    }
  }
}

.fixed-bottom-right {
  position: fixed;
  bottom: 20px;
  right: 20px;
  z-index: 1000;
}

.gitter-open-chat-button {
  background-color: var(--primarycolor);
  color: #fff;
  font-size: 0;
  width: 4rem;
  height: 4rem;
  padding: 0;
  border-radius: 50%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-transform: none;
  bottom: 1rem;
  right: 1rem;
  box-shadow: 7.3px 6.8px 23px 0 rgba(0, 0, 0, 0.08);
}

.gitter-open-chat-button:after {
  content: "Slack";
  font-size: 12px;
}

.gitter-open-chat-button:before {
  content: "";
  display: inline-block;
  vertical-align: top;
  font-size: 22px;
  width: 1em;
  height: 1em;
  background-image: url(data:image/svg+xml;utf8;base64,);
  background-size: contain;
  margin-bottom: 3px;
}

.gitter-open-chat-button.is-collapsed {
  transform: translateY(150%);
}

.gitter-open-chat-button:focus,
.gitter-open-chat-button:hover {
  content: "";
  background-color: #294172;
}

.footer__like {
  width: 1.1em;
  height: 1.1em;
  fill: currentColor;
  fill: #b92d46;
}

.socialNetwork__icon {
  width: 1em;
  height: 1em;
  fill: #fff;
  border: 3px solid #294172;
  padding: 8px;
  font-size: 2.9rem;
  border-radius: 50%;
  margin: 0 0.1em;
  transition: all 0.3s ease-in-out;
}

.socialNetwork__icon:hover {
  /*border-color: #fff;*/
  fill: #294172;
}

.bs-docs-footer {
  background-color: #212121;
  color: rgba(255, 255, 255, 0.5);
  padding: 1.5rem;
  text-align: center;
}

.bs-docs-footer a,
.bs-docs-footer a:link,
.bs-docs-footer a:visited {
  color: #fff;

  /*text-decoration: underline;*/
}

.iubenda-ibadge {
  margin: 0.6rem 0.2rem 0;
}

.iubenda-black {
  background: #212121;
}

.header {
  padding: 2rem 0;
}

.header__desc {
  font-size: 1.4rem;
  font-weight: 400;
  text-align: center;
  color: #757575;
  margin: 1rem 0 0.4rem;
}

.header__title {
  font-size: 2.6rem;
  line-height: 1.3;
  text-align: center;
  color: var(--primarycolor);
  margin-bottom: 0;
  padding-right: 10%;
  padding-left: 10%;
}

.header .btn {
  margin-bottom: 1.8rem;
}

.call-to-action {
  padding-bottom: 1rem;
}

.call-to-action__icon {
  position: absolute;
  right: 1rem;
  bottom: 1rem;
  width: 130px;
  height: 130px;
  opacity: 0.12;
}

.call-to-action__title {
  color: inherit;
}

.call-to-action__desc {
  color: inherit;
  opacity: 0.6;
  margin: 0 0 1rem;
}

.call-to-action__item {
  padding: 2rem 4rem 2rem 3rem;
  color: #353535;
  position: relative;
  box-shadow: 7.3px 6.8px 23px 0 rgba(0, 0, 0, 0.08);
}

.call-to-action__first {
  border-radius: 1rem 0 0 1rem;
}

.call-to-action__second {
  color: #fff;
  border-radius: 0 1rem 1rem 0;
}

.chart {
  padding: 0 1rem 3rem;
}

.nav.nav-tabs {
  margin-bottom: 2rem;
}

.btn:active,
.btn.active {
  -webkit-box-shadow: none;
  box-shadow: none;
}

.container .jumbotron {
  border-radius: 0;
}

.jumbotron {
  font-size: 18px;
}

.jumbotron h1 {
  text-align: left;
}

/*forms*/
.form-control {
  -webkit-box-shadow: none;
  box-shadow: none;
}

.has-error div {
  padding: 5px;
  margin-bottom: 20px;
  display: block;
}

#dockerPullChart,
#dockerPullChart1y,
#dockerPullChart3m {
  max-width: 800px;
  max-height: 400px;
  margin-left: auto;
  margin-right: auto;
}

.table.middle tbody tr td {
  font-size: 1.1em;
  vertical-align: middle;
}

table.ts {
  border-collapse: separate;
  border-spacing: 0.5em 1em;
}

