body {
  font-size: 18px;
  line-height: 1.375;
  -webkit-font-smoothing: antialiased; /* Fix for webkit rendering */
  -webkit-text-size-adjust: 100%;
  margin: 0;
  padding: 4rem 0 0;
  font-family: "Open Sans", sans-serif;
}

.jumbotron {
  background-color: #eee;
}

h3,
legend {
  font-size: 1.6rem;
  margin-bottom: 14px;
  font-weight: 400;
}

h4 {
  font-size: 1.3rem;
  line-height: 22px;
  margin-bottom: 6px;

  /*font-style: bold;*/
  // font-family: 'Noto', sans-serif;
  font-weight: 300;
}

h5 {
  font-style: normal;
  font-weight: 300;
  font-size: 1.116em;
  line-height: 1.5714em;
  margin-bottom: 0.916em;
}

h6 {
  font-weight: 300;
  font-style: italic;
  margin-bottom: 2em;
  font-size: 1em;
  line-height: 1.2;
}

h1 small,
h2 small,
h3 small,
h4 small {
  text-transform: none;
  font-size: 0.5em;
  font-weight: 400;
  color: inherit;
}

p,
ul {
  margin-top: 0;
  margin-bottom: 12px;
}

strong {
  font-weight: bold;
}

li {
  line-height: inherit;
}

a:link,
a:visited {
  text-decoration: none;
}

a:hover,
a:active {
  text-decoration: underline;
}

ul.iconsList li a:hover {
  text-decoration: none;
}

a.readMore {
  display: block;
  margin-top: 0.75em;
  font-style: italic;
}

blockquote {
  font-size: 22px;
  line-height: 33px;
  margin-bottom: 0;
  font-weight: 300;
  padding: 0;
  border: none;
  text-align: left;
}

blockquote p {
  font-size: inherit;
  font-style: italic;
}

blockquote small {
  font-size: 50%;
  display: inline;
}

blockquote p:before {
  color: #294172;
  content: "\e928";
  font-family: "custom-icons";
  margin-right: 0.5em;
}

blockquote p:after {
  color: #294172;
  content: "\e929";
  font-family: "custom-icons";
  margin-left: 0.5em;
}

.quoteRow blockquote:before,
.quoteRow blockquote:after {
  content: none;
}

hr {
  padding: 0;
  margin: 1em 0 1em 0;
}

.hero-unit blockquote {
  font-family: Georgia, "Times New Roman", Times, serif;
  font-style: italic;
}

