/* GENERAL
================================================== */
.slice {
  padding: 1rem 0;
}

/*** buttons ***/
.btn {
  text-transform: capitalize;
  padding: 15px 20px;
  font-size: 18px;
  -webkit-transition: all 0.2s ease-out;
  -moz-transition: all 0.2s ease-out;
  -o-transition: all 0.2s ease-out;
  -ms-transition: all 0.2s ease-out;
  transition: all 0.2s ease-out;
  padding: 1.1em 3em;
  background-color: var(--primarycolor);
  font-size: 0.75rem;
  font-weight: 600;
  line-height: 1.3;
  letter-spacing: 2.4px;
  text-align: center;
  color: #ffffff;
  text-transform: uppercase;
  border-radius: 10rem;
  // font-family: 'Noto', sans-serif;
}

.btn:hover,
.btn:focus,
.btn:active {
  background-color: var(--primarycolor);
  color: rgba(255, 255, 255, 0.5);
}

.btn-o {
  color: #9c283c;
  border: solid 3px var(--primarycolor);
  background: none;
}

.btn-o-white {
  color: #fff;
  border: solid 3px #fff;
  background: none;
}

.btn-lg {
  padding: 1.8em 5.5em;

  /*font-size: 1.3rem;*/
}

.btn-sm {
  padding: 4px 11px;
  font-size: 12px;
  border-radius: 4px;
  letter-spacing: 0.03em;
  text-transform: none;
  font-weight: 600;
}

.btn-xs {
  padding: 4px 7px;
  font-size: 10px;
  border-radius: 4px;
}

.btn-link {
  text-transform: none;
  text-shadow: none;
  padding: 0;
  background: none;
  color: var(--primarycolor);
}

.btn-link:hover,
.btn-link:focus,
.btn-link:active {
  background: none;
  color: var(--primarycolor);
}

.btn i {
  margin-right: 0.5em;
}

/*** call to actioon ***/

.ctaBox {
  padding: 33px 11px;
}

.ctaBoxFullwidth {
  padding: 0;
}

.ctaBox a {
  margin-top: 22px;
}

.ctaBox h1 {
  font-size: 22px;
  line-height: 33px;
  margin-bottom: 0;
  font-weight: 300;
  // font-family: 'Noto', sans-serif;
  padding: 0;
  border: none;
  text-align: left;
  letter-spacing: normal;
  text-transform: none;
}

