  margin-bottom: 0.8rem;
  padding-top: 0.3rem;
  margin-top: 0.3rem;
  position: relative;
  z-index: 2;
}

.docs-head .btn:not(.docs-head__btn) {
  padding: 0.6rem 2rem;
  letter-spacing: 0.05rem;
  text-transform: none;
  font-size: 1rem;
}

.docs-head__btn {
  width: 3.2rem;
  height: 2.7rem;
  padding: 0;
  border-radius: 1rem;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
  margin-right: 0.5rem;
}

.docs-head__btn svg {
  fill: #fff;
}

.bd-content > h2[id],
.bd-content > h3[id],
.bd-content > h4[id] {
  margin-top: 1em;
  color: 0b69c0;
}

// this sets the offset for anchor links
.bd-content h2[id]::before,
.bd-content h3[id]::before,
.bd-content h4[id]::before {
  display: block;
  height: 5rem;
  margin-top: -5rem;
  visibility: hidden;
  content: "";
}

@media (max-width: 991px) {
  .docs-head {
    display: block !important;
  }

  .docs-head h1 {
    margin-bottom: 1rem !important;
  }
}

@media (max-width: 767px) {
  .gitter-open-chat-button {
    display: none;
  }

  .call-to-action__title {
    font-size: 1.2rem;
  }

  .btn {
    letter-spacing: 0.04em;
    padding-left: 2rem;
    padding-right: 2rem;
  }

  .btn-link {
    padding: 0;
  }

  .docs-head__btn {
    padding-left: 0;
    padding-right: 0;
  }

  .newsText__icon {
    font-size: 4rem;
    margin-top: 0;
    margin-bottom: 1rem;
  }

  .call-to-action__item {
    border-radius: 1rem;
    box-shadow: 0 0 23px 0 rgba(0, 0, 0, 0.08);
    padding: 2rem;
  }

  .docs-head .btn:not(.docs-head__btn) {
    display: none;
  }

  .bd-sidebar {
    margin-bottom: 1.5rem;
  }

  .bd-sidebar__submit,
  .bd-sidebar__input {
    height: 2.5rem;
  }

  .bd-sidebar__head {
    height: auto;
    padding: 1rem 0 0.5rem;
  }

  .docs-head {
    padding-bottom: 0.5rem;
    min-height: 3rem;
  }

  .bd-sidebar__menu {
    border-bottom: 1px solid #e8e8e8;
  }
}

.rh4-card-header {
  background: var(--primarycolor);
  color: white;
}

@media (min-width: 767px) {
  .w-md-50 {
    width: 50% !important;
  }
}

div.editions-matrix {
  .comparison {
    max-width: 940px;
    margin: 0 auto;
    font-size: 18px;
    text-align: center;
    padding: 10px;
  }

  @media (max-width: 767px) {
    .comparison {
      font-size: 14px;
    }
  }

  .comparison table {
    width: 100%;
    border-collapse: collapse;
    border-spacing: 0;
    border-bottom: 1px solid #ccc;
  }

  .comparison td,
  .comparison th {
    border-right: 1px solid #ccc;
    empty-cells: show;
    padding: 10px;
  }

  .compare-heading {
    font-weight: bold !important;
    border-bottom: 0 !important;
    padding-top: 10px !important;
  }

  .comparison tbody tr:nth-child(odd) {
    display: none;
  }

  .comparison .compare-row {
    background: #f5f5f5;
  }

  .comparison .highlighted-row {
    background: #e8e8e8;
  }

  .comparison .highlighted-text {
    background: #e8e8e8;
  }

  .comparison .color-primary {
    color: var(--primarycolor);
  }

  .comparison th {
    font-weight: normal;
    padding: 0;
    border-bottom: 1px solid #ccc;
  }

  .comparison tr td:first-child {
    text-align: left;
  }

  .comparison .qbse,
  .comparison .qbo,
  .comparison .tl {
    color: #fff;
    padding: 10px;
    font-size: 13px;
    border-right: 1px solid #ccc;
    border-bottom: 0;
  }

  .comparison .tl2 {
    border-right: 0;
  }

  .comparison .qbse {
    background: var(--primarycolor);
    border-top-left-radius: 3px;
    border-left: 0px;
  }

  .restheart-version {
    color: var(--primarycolor);
  }
  .comparison .qbo {
    background: #009e2c;
    border-top-right-radius: 3px;
    border-right: 0px;
  }

  .comparison .price-info {
    padding: 5px 15px 15px 15px;
  }

  .comparison .price-was {
    color: #999;
    text-decoration: line-through;
  }

  .comparison .price-now,
  .comparison .price-now span {
    color: #ff5406;
  }

  .comparison .price-now span {
    font-size: 32px;
  }

  .comparison .price-small {
    font-size: 18px !important;
    position: relative;
    top: -11px;
    left: 2px;
  }

  .comparison .price-buy {
    background: #ff5406;
    padding: 10px 20px;
    font-size: 12px;
    display: inline-block;
    color: #fff;
    text-decoration: none;
    border-radius: 3px;
    text-transform: uppercase;
    margin: 5px 0 10px 0;
  }

  .comparison .price-try {
    font-size: 12px;
  }

  .comparison .price-try a {
    color: #202020;
  }

  @media (max-width: 767px) {
    .comparison td:first-child,
    .comparison th:first-child {
      display: none;
    }
    .comparison tbody tr:nth-child(odd) {
      display: table-row;
      background: #f7f7f7;
    }
    .comparison .row {
      background: #fff;
    }
    .comparison td,
    .comparison th {
      border: 1px solid #ccc;
    }
    .price-info {
      border-top: 0 !important;
    }
  }

  @media (max-width: 639px) {
    .comparison .price-buy {
      padding: 5px 10px;
    }
    .comparison td,
    .comparison th {
      padding: 10px 5px;
    }
    .comparison .hide-mobile {
      display: none;
    }
    .comparison .price-now span {
      font-size: 16px;
    }

    .comparison .price-small {
      font-size: 16px !important;
      top: 0;
      left: 0;
    }
    .comparison .qbse,
    .comparison .qbo {
      font-size: 12px;
      padding: 10px 5px;
    }
    .comparison .price-buy {
      margin-top: 10px;
    }
    .compare-heading {
      font-size: 13px;
    }
  }
}

.faq-toc {
  position: -webkit-sticky;
  position: sticky;
  top: 4rem;
  height: calc(120vh - 4rem);
  z-index: 10;
  overflow-y: auto;
  border-right: 1px solid #e8e8e8;
  background-color: #fcfcfc;
  padding: 15px 15px 15px 5px;
}
.faq-toc ul {
  padding-left: 10px;
  list-style: none;
  font-size: 16px;
  line-height: 20.8px;
}
.faq-toc li {
  padding: 0.2em 0;
}
.faq-toc a {
  color: #666;
}

.anchor-offset {
  display: block;
  height: 3.5rem;
  margin-top: -3.5rem;
  visibility: hidden;
  content: "";
}

.article-font-size {
  p {
    font-size: 24px;
  }

  h1 {
    font-size: 36px;
  }
}

.trial-btn {
  background: floralwhite;
  color: var(--primarycolor);
  cursor: pointer;
  border-radius: 10rem;
  text-transform: capitalize;
  padding: 15px 20px;
  font-size: 18px;
  padding: 1.1em 3em;
  font-size: 0.75rem;
  font-weight: 600;
  line-height: 1.3;
  letter-spacing: 2.4px;
  text-align: center;
  text-transform: uppercase;
  // font-family: 'Noto', sans-serif;
}

.trial-btn:focus {
  outline: 0;
}

.write-us-btn:hover,
.write-us-btn:focus,
.write-us-btn:active {
  background-color: var(--primarycolor);
  color: rgba(255, 255, 255, 2.5);
}

.write-us-btn {
  background: floralwhite;
  color: var(--primarycolor);
  cursor: pointer;
  border-radius: 10rem;
  padding: 15px 20px;
  font-size: 18px;
  padding: 1.1em 3em;
  font-weight: 600;
  line-height: 1.3;
  letter-spacing: 2.4px;
  text-align: center;
  // font-family: 'Noto', sans-serif;
}

.bs-callout {
  padding: 20px;
  margin: 20px 0;
  border: 1px solid #eee;
  border-left-width: 5px;
  border-radius: 3px;

  h4 {
    margin-top: 0;
    margin-bottom: 5px;
  }

  p:last-child {
    margin-bottom: 0;
  }

  code {
    border-radius: 3px;
  }

  + .bs-callout {
    margin-top: -5px;
  }
}

.bs-callout-danger {
  border-left-color: rgb(248, 215, 218);

  h4 {
    color: rgb(114, 28, 36);
  }
}

.bs-callout-warning {
  border-left-color: rgb(255, 243, 205);

  h4 {
    color: rgb(133, 100, 4);
  }
}

.bs-callout-info {
  border-left-color: rgb(209, 236, 241);

  h4 {
    color: rgb(12, 84, 96);
  }
}

.bs-callout-success {
  border-left-color: #d4edda;

  h4 {
    color: rgb(12, 84, 96);
  }
}

.fs-16 {
  font-size: 16px !important;
}

.data-lang-attr {
  position: absolute;
  font-size: 0.75em;
  top: 0.425rem;
  right: 0.5rem;
  line-height: 1;
  text-transform: uppercase;
  color: #999;
}
