// Utility Classes
// ---------------

// Spacing Utilities
.m-0 { margin: 0 !important; }
.mt-0 { margin-top: 0 !important; }
.mr-0 { margin-right: 0 !important; }
.mb-0 { margin-bottom: 0 !important; }
.ml-0 { margin-left: 0 !important; }

.p-0 { padding: 0 !important; }
.pt-0 { padding-top: 0 !important; }
.pr-0 { padding-right: 0 !important; }
.pb-0 { padding-bottom: 0 !important; }
.pl-0 { padding-left: 0 !important; }

// Spacing with steps
@for $i from 1 through 5 {
  .m-#{$i} { margin: #{$i * 0.25}rem !important; }
  .mt-#{$i} { margin-top: #{$i * 0.25}rem !important; }
  .mr-#{$i} { margin-right: #{$i * 0.25}rem !important; }
  .mb-#{$i} { margin-bottom: #{$i * 0.25}rem !important; }
  .ml-#{$i} { margin-left: #{$i * 0.25}rem !important; }

  .p-#{$i} { padding: #{$i * 0.25}rem !important; }
  .pt-#{$i} { padding-top: #{$i * 0.25}rem !important; }
  .pr-#{$i} { padding-right: #{$i * 0.25}rem !important; }
  .pb-#{$i} { padding-bottom: #{$i * 0.25}rem !important; }
  .pl-#{$i} { padding-left: #{$i * 0.25}rem !important; }
}

// Text Alignment
.text-left { text-align: left !important; }
.text-right { text-align: right !important; }
.text-center { text-align: center !important; }
.text-justify { text-align: justify !important; }

// Text Transform
.text-lowercase { text-transform: lowercase !important; }
.text-uppercase { text-transform: uppercase !important; }
.text-capitalize { text-transform: capitalize !important; }

// Font Weights
.font-weight-light { font-weight: 300 !important; }
.font-weight-normal { font-weight: 400 !important; }
.font-weight-bold { font-weight: 700 !important; }

// Display Utilities
.d-block { display: block !important; }
.d-inline { display: inline !important; }
.d-inline-block { display: inline-block !important; }
.d-flex { display: flex !important; }
.d-none { display: none !important; }

// Flex Utilities
.flex-row { flex-direction: row !important; }
.flex-column { flex-direction: column !important; }
.flex-wrap { flex-wrap: wrap !important; }
.flex-nowrap { flex-wrap: nowrap !important; }
.justify-content-start { justify-content: flex-start !important; }
.justify-content-end { justify-content: flex-end !important; }
.justify-content-center { justify-content: center !important; }
.justify-content-between { justify-content: space-between !important; }
.align-items-start { align-items: flex-start !important; }
.align-items-end { align-items: flex-end !important; }
.align-items-center { align-items: center !important; }

// Visibility
.visible { visibility: visible !important; }
.invisible { visibility: hidden !important; }

// Overflow
.overflow-auto { overflow: auto !important; }
.overflow-hidden { overflow: hidden !important; }
.overflow-visible { overflow: visible !important; }

// Position
.position-relative { position: relative !important; }
.position-absolute { position: absolute !important; }
.position-fixed { position: fixed !important; }

// Width Utilities
.w-25 { width: 25% !important; }
.w-50 { width: 50% !important; }
.w-75 { width: 75% !important; }
.w-100 { width: 100% !important; }

// Height Utilities
.h-25 { height: 25% !important; }
.h-50 { height: 50% !important; }
.h-75 { height: 75% !important; }
.h-100 { height: 100% !important; }

// Border Utilities
.border { border: 1px solid $border-color !important; }
.border-top { border-top: 1px solid $border-color !important; }
.border-right { border-right: 1px solid $border-color !important; }
.border-bottom { border-bottom: 1px solid $border-color !important; }
.border-left { border-left: 1px solid $border-color !important; }
.border-0 { border: 0 !important; }

// Rounded Corners
.rounded { border-radius: $border-radius !important; }
.rounded-top { border-top-left-radius: $border-radius !important; border-top-right-radius: $border-radius !important; }
.rounded-right { border-top-right-radius: $border-radius !important; border-bottom-right-radius: $border-radius !important; }
.rounded-bottom { border-bottom-left-radius: $border-radius !important; border-bottom-right-radius: $border-radius !important; }
.rounded-left { border-top-left-radius: $border-radius !important; border-bottom-left-radius: $border-radius !important; }
.rounded-circle { border-radius: 50% !important; }

// Responsive Utilities
@each $breakpoint in (sm, md, lg, xl) {
  @media (min-width: map-get($grid-breakpoints, $breakpoint)) {
    .d-#{$breakpoint}-none { display: none !important; }
    .d-#{$breakpoint}-block { display: block !important; }
    .d-#{$breakpoint}-flex { display: flex !important; }
  }
}

// Background Colors
.bg-primary { background-color: $color-primary !important; }
.bg-secondary { background-color: $color-secondary !important; }
.bg-success { background-color: #28a745 !important; }
.bg-danger { background-color: #dc3545 !important; }
.bg-warning { background-color: #ffc107 !important; }
.bg-info { background-color: #17a2b8 !important; }
.bg-light { background-color: #f8f9fa !important; }
.bg-dark { background-color: #343a40 !important; }
.bg-white { background-color: #fff !important; }
.bg-transparent { background-color: transparent !important; }

// Text Colors
.text-primary { color: $color-primary !important; }
.text-secondary { color: $color-secondary !important; }
.text-success { color: #28a745 !important; }
.text-danger { color: #dc3545 !important; }
.text-warning { color: #ffc107 !important; }
.text-info { color: #17a2b8 !important; }
.text-light { color: #f8f9fa !important; }
.text-dark { color: #343a40 !important; }
.text-white { color: #fff !important; }
