@charset "utf-8";

// Core
@import "variables";
@import "bootstrap/bootstrap";

// Asciidoctor
@import "asciidoctor.css";

// Base styles
@import "base";
@import "layout";
@import "components";
@import "syntax";
@import "utilities";

@import "infinite-slider";

html {
  scroll-behavior: smooth;
  --app-height: 100%;
}

body {
  background-color: $background-color;
  color: $text-color;
}

// Mobile responsive classes
@media screen and (max-width: $breakpoint-md) {
  .mobile-hidden {
    display: none;
  }
}

// Text sizing classes
.top-1 {
  font-size: 5.5vw;
}
.top-2 {
  font-size: 3.4vw;
}
.top-3 {
  font-size: 3vw;
}
.top-4 {
  font-size: 2.2vw;
}
.top-5 {
  font-size: 2.1vw;
}
.top-6 {
  font-size: 1.9vw;
}

@media screen and (max-width: $breakpoint-md) {
  .top-1 {
    font-size: 42px;
  }
  .top-2 {
    font-size: 26px;
  }
  .top-3 {
    font-size: 24px;
  }
  .top-4 {
    font-size: 20px;
  }
  .top-5 {
    font-size: 18px;
  }
  .top-6 {
    font-size: 16px;
  }

  .lh-1_4 {
    line-height: 1.4 !important;
  }
  .lh-1_3 {
    line-height: 1.3 !important;
  }
  .lh-1_2 {
    line-height: 1.2 !important;
  }
}

@media screen and (min-width: $breakpoint-xl) {
  .top-1 {
    font-size: 64px;
  }
  .top-2 {
    font-size: 42px;
  }
  .top-3 {
    font-size: 38px;
  }
}

// Utility classes
.text-break {
  overflow-wrap: break-word;
  word-break: break-word;
}

.background-black {
  background-color: $color-black;
}

.text-lightcyan {
  color: lightcyan;
}

.display-5 {
  font-size: 1.7rem;
  font-weight: 300;
}

// Width utilities
.mw-1024 {
  max-width: 1024px;
}
.mw-800 {
  max-width: 800px;
}
.mw-keypoint {
  max-width: 400px;
}

// Zoom effect
.zoom {
  transition: transform 0.5s ease;

  &:hover {
    transform: scale(1.1);
    filter: brightness(95%);
    z-index: 1;
  }
}

// IFrame adjustments
.iframe-parent {
  height: calc(var(--vh, 1vh) * 100 - 64px);
  border: none;
}

iframe {
  margin-top: 0;
  height: calc(var(--vh, 1vh) * 100 - 64px);
  width: 100%;
  border: none;
}

// Font size adjustments
.fs-16 {
  font-size: 16px;
}

// Code language display
.data-lang-attr {
  &::before {
    content: attr(data-lang);
    position: absolute;
    right: 1rem;
    top: 0.5rem;
    color: $color-gray;
    font-size: 0.8em;
  }
}
